.drawer-backdrop-enter {
  opacity: 0;
}
.drawer-backdrop-enter-active {
  opacity: 1;
  transition: opacity 200ms ease-out;
}
.drawer-backdrop-exit {
  opacity: 1;
}
.drawer-backdrop-exit-active {
  opacity: 0;
  transition: opacity 200ms ease-out;
}
.drawer-panel-enter {
  transform: translateX(100%);
}
.drawer-panel-enter-active {
  transform: translateX(0);
  transition: transform 300ms ease-out;
}
.drawer-panel-exit {
  transform: translateX(0);
}
.drawer-panel-exit-active {
  transform: translateX(100%);
  transition: transform 300ms ease-out;
}
.text-primary-color {
  color: hsl(var(--primary));
}
.text-success-color {
  color: hsl(var(--success));
}
.text-destructive-color {
  color: hsl(var(--destructive));
}
.carousel-word {
  opacity: 0;
  animation: carousel 6s linear infinite;
  white-space: normal;
  font-size: clamp(2rem, 6vw, 4.5rem);
  display: block;
  line-height: 1.2;
  width: 100%;
  overflow-wrap: break-word;
  word-wrap: break-word;
  hyphens: auto;
}
.carousel-word:nth-child(1) {
  animation-delay: 0s;
}
.carousel-word:nth-child(2) {
  animation-delay: 2s;
}
.carousel-word:nth-child(3) {
  animation-delay: 4s;
}
@keyframes carousel {
  0%, 10% {
    opacity: 0;
    transform: translateY(10px);
  }
  15%, 35% {
    opacity: 1;
    transform: translateY(0);
  }
  40%, 100% {
    opacity: 0;
    transform: translateY(-10px);
  }
